* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Gotham, Helvetica, Arial, sans-serif;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

body{
  line-height: 1.4;
}

a{
  color: #6ccaff;
  text-shadow: 1px 1px 1px rgba(86, 86, 88, 0.98);
  text-decoration: none;
}

.container{
  padding: 0;
}

.btn {
  display: inline-block;
  border: none;
  background: #555;
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
}

.btn:hover{
  background: #666;
}